/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { Box, Typography, Button, Grid, makeStyles, Paper, Divider } from '@material-ui/core';
import { ReactNode } from 'react';
import * as Icon from 'react-feather';
import SimplePostItem from 'components/Items/SimplePostItem';
import { Post } from 'models/post';
import Image, { FluidObject } from 'gatsby-image';
import { Colors } from 'utils/colors';

interface Props {
  title: string;
  children: ReactNode;
  thumbnail: FluidObject;
  githubLink?: string;
  demoLink?: string;
  posts?: Post[];
  divider?: boolean;
}
const ProjectIntroduction = ({
  title,
  children,
  thumbnail,
  githubLink,
  demoLink,
  posts = [],
  divider = true,
}: Props) => {
  const buttonStyle = makeStyles({
    root: {
      backgroundColor: Colors.Grey['900'],
      color: Colors.Grey['100'],
      width: '100%',
      '&:hover': {
        backgroundColor: Colors.Grey['700'],
        color: Colors.Grey['100'],
      },
    },
  })();

  return (
    <Box>
      <Typography variant="h5" component="h3" gutterBottom>
        {title}
      </Typography>
      <Image
        css={css`
          border-radius: 6px;
          margin-bottom: 1rem;
        `}
        fluid={thumbnail}
      />
      <Grid
        spacing={1}
        container
        css={css`
          margin-bottom: 2rem;
        `}
        justify="center"
      >
        {githubLink && (
          <Grid item>
            <Button href={githubLink} className={buttonStyle.root}>
              <Icon.GitHub />
            </Button>
          </Grid>
        )}
        {demoLink && (
          <Grid item>
            <Button href={demoLink} className={buttonStyle.root}>
              <Icon.Play />
            </Button>
          </Grid>
        )}
      </Grid>
      {children}
      <Box
        css={css`
          margin-top: 1rem;
        `}
      >
        <Paper
          css={css`
            padding: 1rem;
          `}
        >
          <Typography variant="h6" component="h4" gutterBottom>
            관련 포스팅
          </Typography>
          {posts.map(({ frontmatter, fields }, index) => (
            <SimplePostItem
              key={fields.path ?? index}
              thumbnail={frontmatter.thumbnail.childImageSharp?.fixed?.src ?? ''}
              title={frontmatter.title ?? ''}
              date={frontmatter.date}
              path={fields.path ?? '/'}
              categories={frontmatter.categories}
            />
          ))}
        </Paper>
      </Box>
      {divider && (
        <Divider
          css={css`
            margin: 2rem 0;
          `}
        />
      )}
    </Box>
  );
};

export default ProjectIntroduction;
